export const getCounselors = {
    data() {
        return {
        }
    },
    created() {
        if(this.$store.state.counselors.length<=0)
            this.$store.dispatch('fetchCounselors')
    },
    methods: {
        querySearch(queryString, cb) {
            var counselors = this.$store.state.counselors
            counselors.forEach(element => {
                element.value = element.name
            })
            var results = queryString ? counselors.filter(this.createFilter(queryString)) : counselors;
            cb(results);
        },
        createFilter(queryString) {
            return (counselor) => {
                return (counselor.value.toLowerCase().indexOf(queryString.toLowerCase()) >= 0);
            };
        }
    }
}